<template>
  <div class="list">
    <Row>
      <Col>
      <Card>
        <div slot="title">
          <Icon type="ios-film-outline"></Icon>
          
          <Button type="dashed" @click.native="fixedHeader=!fixedHeader">
            <Icon type="pin"></Icon>
          </Button>
          <Input v-model="formItem.input" placeholder="" style="width:187px"></Input>
          <Button type="info" icon="ios-search" shape="circle"></Button>
        </div>
        <Table :loading="loading2" :show-header="showHeader" :height="fixedHeader ? 300 : ''" :size="tableSize"
               :data="listData"
               :columns="columns1" ref="table" @on-select="onSelect" @on-selection-change="onSelectionChange"></Table>
        <Page :total="100" show-sizer show-elevator @on-change="pageChange" style="margin-top: 10px"
              @on-page-size-change="PageSizeChange"></Page>
      </Card>
      </Col>
    </Row>

    <!--  -->
    <Modal v-model="deleteTip" width="360">
      <p slot="header" style="color:#f60;text-align:center">
        <Icon type="information-circled"></Icon>
        <span></span>
      </p>
      <div style="text-align:center">
        <p>{{selection.length}},</p>
        <p></p>
      </div>
      <div slot="footer">
        <Button type="error" size="large" long @click="deleteBatch"></Button>
      </div>
    </Modal>
    <!-- / -->
    <!--  -->
    <Modal v-model="editModal" v-if='DateReady'>
      <p slot="header" style="text-align:center">
        <Icon type="information-circled"></Icon>
        <span v-if="currIndex==-1"></span>
        <span v-if="currIndex!=-1"></span>
      </p>
      <div style="text-align:center">
        <Form :model="formItem" :label-width="80">
          <Form-item label="">
            <Input v-model="currDate.who" placeholder=""></Input>
          </Form-item>
          <Form-item label="">
            <Date-picker type="date" placeholder="" style="width:100%" v-model="currDate.createdAt"
                         :editable='false'></Date-picker>
          </Form-item>
          <Form-item label="">
            <Date-picker type="date" placeholder="" style="width:100%" v-model="currDate.publishedAt"
                         :editable='false'></Date-picker>
          </Form-item>
          <Form-item label="">
            <Select v-model="currDate.type" placeholder="">
              <Option value="Android">Android</Option>
              <Option value="iOS">iOS</Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value="App">App</Option>
            </Select>
          </Form-item>

          <Form-item label="">
            <Input v-model="currDate.desc" type="textarea" :autosize="{minRows: 2,maxRows: 5}"
                   placeholder="..."></Input>
          </Form-item>
        </Form>
      </div>
      <div slot="footer">
        <Button type="success" size="large" long @click.native="saveBatch" :loading="loading" :disabled="saveDisabled">
          
        </Button>
      </div>
    </Modal>
    <!-- / -->
  </div>
</template>
<script>
  import elementResizeDetectorMaker from 'element-resize-detector'
  var erd = elementResizeDetectorMaker()
  export default {
    name: 'list',
    components: {},
    data () {
      return {
        formItem: {
          input: '',
          select: '',
          date: '',
          time: '',
          radio: '',
          checkbox: []
        },
        searchState: true,
        editModal: false,
        detailModal: false,
        deleteTip: false,
        showHeader: true, //  @:show-header
        loading2: false, // loading
        fixedHeader: false, //  @:height
        tableSize: 'small', // @:size
        DateReady: false, // 
        loading: false, // save
        currDate: {}, // 
        currIndex: 0, // 
        saveDisabled: false,
        params: {
          page: 1,
          limit: 10,
          category: 'Android'
        },
        selection: [], // 
        listData: [], // @:data
        columns1: [
          { // @:columns
            type: 'index', // 
            width: 60,
            align: 'center',
            title: ''
          },
          {
            title: '',
            key: 'type'
          },
          {
            title: '',
            key: 'desc',
            className: 'min-width'
          },
          {
            title: '',
            key: 'action',
            width: 170,
            fixed: 'right',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'small'
                  },
                  style: {
                    color: '#5cadff'
                  },
                  on: {
                    click: () => {
                      this.edit(params.index)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'small'
                  },
                  style: {
                    color: '#ff3300'
                  },
                  on: {
                    click: () => {
                      this.remove(params.index)
                    }
                  }
                }, '')
              ])
            }
          }
        ]
      }
    },
    watch: {
      /**
       * @params 
       * */
      params: {
        handler (val) {
          this.getData(val)
        },
        deep: true
      },
      fixedHeader: {
        handler (val) {
          if (val) {
            this.$Message.info('')
          }
        }
      },
      currDate: {
        handler (val) {
          for (let i = 0; i < Object.values(val).length; i++) {
            if (Object.values(val)[i] === '') {
              this.saveDisabled = true
              return
            } else {
              this.saveDisabled = false
            }
          }
        },
        deep: true
      }
    },
    computed: {
      state () {
        return this.$store.state.app
      }
    },
    methods: {
      searchShow () {
        this.searchState = !this.searchState
      },
      /**
       * 
       * */
      refresh () {
        this.getData(this.params)
      },
      /**
       * @params:category  page limit:
       * */
      getData (params) {
        this.loading2 = true
        this.$api.orderList(params).then((res) => {
          if (!res.error) {
            this.listData = res.results
            this.DateReady = true
            this.loading2 = false
          } else {
            this.$Message.error(res.msg)
            this.loading2 = false
          }
        })
      },
      /**
       * @on-change 
       * */
      pageChange (page) {
        this.params.page = page
      },
      /**
       * @on-page-size-change 
       * */
      PageSizeChange (limit) {
        this.params.limit = limit
      },
      /**
       * 
       * @show 
       * @remove 
       * @edit
       */
      show (index) {
        this.currIndex = index
        this.currDate = this.listData[index]
        this.detailModal = true
        this.$Modal.info({
          title: '',
          content: `${this.listData[index].who}<br>${this.listData[index].type}<br>${this.listData[index].desc}`
        })
      },
      remove (index) {
        this.listData.splice(index, 1)
      },
      edit (index) {
        this.editModal = true
        this.currIndex = index
        if (index === -1) { // 
          this.currDate = {
            createdAt: '',
            desc: '',
            publishedAt: '',
            type: '',
            used: true,
            who: '',
            url: 'c.fwone.com'
          }
        } else { // 
          this.currDate = this.listData[index]
        }
      },
      /**
       * 
       */
      deleteBatch () {
        this.deleteTip = false
        // ...
      },
      saveBatch () {
        this.loading = true
        setTimeout(() => {
          this.loading = false
          this.$Message.info('')
          this.editModal = false
        }, 3000)
      },
      /**
       * 
       * @ type 1  2
       */
      exportData (type) {
        if (type === 1) {
          this.$refs.table.exportCsv({
            filename: ''
          })
        } else if (type === 2) {
          this.$refs.table.exportCsv({
            filename: '',
            original: false
          })
        }
      },
      /**
       * 
       * selection row
       */
      onSelect (selection, row) {
        // console.log(selection,row)
      },
      onSelectionChange (selection) {
        this.selection = selection
      }
    },
    created () {
      this.getData(this.params)
    },
    mounted () {
      erd.listenTo(window, 'resize', this.handleResize)
    }
  }
</script>
<style lang="less" scoped>
  .search-filter {
    opacity: 0;
    display: none;
    overflow: hidden;
    transition: all 0.28s ease-out;
  &.active {
     opacity: 1;
     display: block;
   }
  }
  </st




